<template>
  <div class="waiter">
    <van-nav-bar title="我是客服" left-text="返回" left-arrow :fixed="true" @click-left="back" />
    <div class="talk">
      <div>
        <div class="clearfix item" v-for="(item,index) in chatData" :key="index">
            <div class="headimg" :class="[item.type == 'robot' ? 'fl' : 'fr','pad']">
                <img :src="[item.type == 'robot' ? require('../assets/images/kefu.png'): require('../assets/images/renwu.png')]" alt="" class="auto-img">
            </div>
          <div class="content fl" :class="[item.type == 'robot' ? 'fl' : 'fr', item.type]" >{{item.text}}</div>
        </div>
      </div>
    </div>
     <div class="bottom">
        <div class="inputsend">
        <input v-model="message" type="text" />
        <button @click="sendMsg" class="send">发送</button>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //聊天内容
      chatData: [],
      // 输入内容
      message: "",
      //发送消息
      msg: "",
      //机器人样本
      robotData: [
        {
          msg: '你好',
          text: "你好，请问有什么能帮的上您?",
        },
        {
          msg: 1,
          text: "请联系商家解决",
        },
         {
          msg: 2,
          text: "可以选择有效优惠券",
        },
        {
          msg: 3,
          text: "选择商品后提交订单,填写地址信息，就下单完成了",
        },
        {
          msg: 4,
          text: "是的，都是由我们咖啡师现时手工调制",
        },
        {
          msg: 5,
          text: "满意的话可以加入收藏,不满意的话可以选择其他商品",
        },
      ],
    };
  },
  methods: {
    //返回上一级
    back() {
      this.$router.go(-1);
    },
    //发送消息
    sendMsg() {
      this.msg = this.message;
      this.chatData.push({ type: "person", text: this.msg });
      this.message = "";
    },
  },
  watch: {
    msg(newValue) {
      //通知机器人
      // 
      //匹配样本
      for (let i = 0; i < this.robotData.length; i++) {
        if (newValue == this.robotData[i].msg) {
          setTimeout(() => {
            this.chatData.push({ text: this.robotData[i].text, type: "robot" });
          }, 800);
          return;
        }
      }

      //如果无法匹配样本
      setTimeout(() => {
        this.chatData.push({ text: "你可以回复数字1~10询问,例如:1.下单失败。2.能不能便宜一点。3.怎么下单。4.是纯手工的吗。5.评价......", type: "robot" });   
      }, 800);
    },
  },
};
</script>

<style lang="less" scoped>
.waiter {
  padding-top: 46px;
  padding-bottom: 50px;
  height: 100%;
  .talk{
    width: 100%;
    height:100%;
   background:#fff;
   padding-top: 20px;
  }

  .bottom {
    position: fixed;
    bottom:0;
    left: 0;
    height: 50px;
    width: 100%;
    background-color: #ddd;
    input{
      height: 20px;
    }
    .send{
        height: 24px;
    }
  }
  .inputsend{
      width: 200px;
      margin: 12px auto;
  }
  .item {
    margin-bottom: 20px;
  }

  .content {
    max-width: 240px;
    padding: 10px;
  }

  .robot {
    background-color: #7e7e7e;
  }

  .person {
    background-color: #5f56ff;
  }
  .headimg{
      width: 30px;
      height: 30px;
      border-radius: 50%;
      overflow: hidden;
  }
  .pad{
      padding:0 5px;
  }
  img{
      width: 30px;
      height: 30px;
  }
}
</style>